html, body{
    background-color:#02a0f0;
}
.containerPrincipal{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /*border: 1px solid #1a5276;*/
    border-radius: 8px;
    box-shadow: 12px 12px 150px #1adadd;
    padding: 40px;
    background-color: white;
    font-family: 'Roboto', sans-serif;
}
.containerSecundario label{
    font-family: 'Roboto', sans-serif;
    font-size:14px;
}
.containerSecundario button{
    float: right;
}
.title-login{
    margin-bottom:50px;
    font-size:24px;
}

.container-home{
    padding-left:270px;
    padding-right:20px;
    padding-top:20px;
    background:white;
    font-family:'Roboto', sans-serif;
}
.container-child-home{
    height:85.5vh;
}
.container-footer-home{
    color:#ffbe32;
    font-family:'Roboto', sans-serif;
    font-size:14px;
    height:10%;
    line-height:30px;
    background:#222324;
    text-align:center;
    position:absolute;
    bottom:0;
    left:0;
